import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Container from '@material-ui/core/Container'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import { ChevronRight } from '@material-ui/icons'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import SectionHeader from '../../components/sections-header/section-header'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
  },
  itemIcon: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
})
const WhyUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        {
          data: allSectionsJson(filter: { id: { eq: "why-us" } }) {
            nodes {
              id
              title
              reasons {
                text
              }
            }
          }
        }
      `}
      render={({ data }) => {
        const { title, reasons } = data.nodes[0]
        return (
          <Container className={classes.root}>
            <SectionHeader title={title} />
            <Card className={classes.content} raised={true}>
              <CardContent>
                <List
                  itemScope={true}
                  itemType={'https://schema.org/BusinessAudience'}
                >
                  {reasons.map(({ text }, index) => (
                    <ListItem
                      key={index}
                      alignItems={'flex-start'}
                      dense={true}
                    >
                      <ListItemIcon className={classes.itemIcon}>
                        <ChevronRight />
                      </ListItemIcon>
                      <ListItemText
                        primary={
                          <Typography variant={'body1'}>{text}</Typography>
                        }
                        itemProp={'potentialAction'}
                      />
                    </ListItem>
                  ))}
                </List>
              </CardContent>
            </Card>
          </Container>
        )
      }}
    />
  )
}

WhyUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(WhyUs)
