import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import CtaButton from '../../components/cta-button/cta-button'
import SectionHeader from '../../components/sections-header/section-header'

import logo from '../../images/compose-us-black.svg'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
  },
})

const ContactUs = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        {
          data: allSectionsJson(filter: { id: { eq: "contact" } }) {
            nodes {
              id
              title
              desc
              telephone
            }
          }
        }
      `}
      render={({ data }) => {
        const sectionDetails = data.nodes[0]

        return (
          <Container
            className={classes.root}
            itemScope={true}
            itemType={'https://schema.org/LocalBusiness'}
          >
            <meta itemProp={'name'} content={'compose.us GmbH'} />
            <meta itemProp={'image'} content={logo} />
            <SectionHeader title={sectionDetails.title} />

            <Card className={classes.content} raised={true}>
              <CardContent>
                <Typography variant={'body1'} align={'left'} component={'p'}>
                  {sectionDetails.desc}
                </Typography>
                <CtaButton telephone={sectionDetails.telephone} />
              </CardContent>
            </Card>
          </Container>
        )
      }}
    />
  )
}

ContactUs.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(ContactUs)
