import React from 'react'
import PropTypes from 'prop-types'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Chip from '@material-ui/core/Chip'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import withWidth, { isWidthDown } from '@material-ui/core/withWidth'

import capitalize from '../../lib/capitalize'
import technologies from '../../lib/technologies'

const TechnologiesCategoryList = ({ categories, callback, morph }) => {
  let listStyle = {}
  let listItemStyle = { textAlign: 'right' }

  if (morph) {
    listStyle.display = 'flex'
    listStyle.flexWrap = 'wrap'
    listItemStyle.textAlign = 'center'
    listItemStyle.width = 'auto'
  }

  return (
    <List dense={true} style={listStyle}>
      {categories.map((category, index) => (
        <ListItem key={index} button style={listItemStyle}>
          <ListItemText
            primary={capitalize(category)}
            onMouseEnter={() => callback(category)}
            onMouseLeave={() => callback(null)}
            onClick={() => callback(category)}
          />
        </ListItem>
      ))}
    </List>
  )
}

TechnologiesCategoryList.propTypes = {
  categories: PropTypes.array.isRequired,
  callback: PropTypes.func.isRequired,
  minimize: PropTypes.bool,
}

const TechnologyList = ({ list, selected }) => {
  return (
    <Grid container spacing={1}>
      {list.map((item, index) => {
        const isSelected = item.categories.indexOf(selected) >= 0
        return (
          <Grid key={index} item>
            <TechnologyListItem
              label={item.label}
              categories={item.categories}
              selected={isSelected}
            />
          </Grid>
        )
      })}
    </Grid>
  )
}

TechnologyList.propTypes = {
  list: PropTypes.array.isRequired,
  selected: PropTypes.string,
}

const TechnologyListItem = ({ label, categories, selected }) => {
  const handleEvent = () => {
    // TODO: Trigger category highlight
  }

  if (selected) {
    return (
      <Chip
        label={label}
        color={'primary'}
        onMouseEnter={handleEvent}
        onClick={handleEvent}
      />
    )
  } else {
    return (
      <Chip label={label} onMouseEnter={handleEvent} onClick={handleEvent} />
    )
  }
}

TechnologyListItem.propTypes = {
  label: PropTypes.string.isRequired,
  categories: PropTypes.array.isRequired,
}

const TechnologiesCard = ({ width }) => {
  const possibleTechnologies = technologies.reduce(
    (acc, { categories }) => ({
      ...acc,
      ...categories.reduce((cats, c) => ({ ...cats, [c]: false }), {}),
    }),
    {}
  )
  const categories = Object.keys(possibleTechnologies)
  const [category, setCategory] = React.useState(null)
  const isMorphed = isWidthDown('sm', width)

  return (
    <Container>
      <Card raised={true}>
        <CardContent>
          <Grid container direction={'row'} spacing={2}>
            <Grid item sm={12} md={3}>
              <Typography variant={'h6'} color={'primary'}>
                Technologien
              </Typography>
              <TechnologiesCategoryList
                categories={categories}
                callback={setCategory}
                morph={isMorphed}
              />
            </Grid>
            <Grid item sm={12} md={9}>
              <TechnologyList list={technologies} selected={category} />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Container>
  )
}

TechnologiesCard.propTypes = {
  width: PropTypes.string.isRequired, // breakpoint string from MUI
}

export default withWidth()(TechnologiesCard)
