import React from 'react'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'
import Image from '../image/image'

const styles = theme => ({
  root: {
    ...theme.base.section.root,
    height: '100%',
  },
  media: {
    [theme.breakpoints.up('md')]: {
      height: '320px',
    },
    [theme.breakpoints.down('md')]: {
      height: '240px',
    },
    [theme.breakpoints.down('sm')]: {
      height: '160px',
    },
  },
})

const CarouselCard = ({ classes, data }) => (
  <Card className={classes.root}>
    <div itemScope={true} itemType={'https://schema.org/CreativeWork'}>
      <Image classes={classes.media} src={data.image} alt={data.title} />
      <CardContent>
        <Typography
          gutterBottom
          variant={'h5'}
          align={'left'}
          itemProp={'name'}
        >
          {data.title}
        </Typography>
        <Typography
          gutterBottom
          variant={'body1'}
          align={'left'}
          className={classes.desc}
          itemProp={'description'}
        >
          {data.desc}
        </Typography>
      </CardContent>
    </div>
  </Card>
)

export default withStyles(styles)(CarouselCard)
