import React from 'react'
import PropTypes from 'prop-types'
import { graphql, StaticQuery } from 'gatsby'

import withStyles from '@material-ui/styles/withStyles'
import SectionHeader from '../sections-header/section-header'
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import CarouselCard from '../carousel-card/carousel-card'
import Container from '@material-ui/core/Container'
import Carousel from '../carousel/carousel'

const styles = theme => ({
  root: {
    ...theme.base.section.root,
  },
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0),
    [theme.breakpoints.down('xs')]: {
      padding: theme.spacing(0),
      margin: [
        theme.spacing(0),
        theme.spacing(0),
        theme.spacing(2),
        theme.spacing(0),
      ],
    },
  },
})

const CaseStudies = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        {
          data: allSectionsJson(filter: { id: { eq: "case-studies" } }) {
            nodes {
              id
              title
              desc
            }
          }
          caseStudies: allCaseStudiesJson {
            nodes {
              id
              title
              desc
              image
            }
          }
        }
      `}
      render={({ data, caseStudies }) => {
        const { title, desc } = data.nodes[0]
        return (
          <Container className={classes.root}>
            <SectionHeader title={title} />
            <Typography variant={'body1'} align={'center'} component={'span'}>
              {desc}
            </Typography>

            <Grid
              container
              direction={'row'}
              justify={'space-between'}
              alignItems={'stretch'}
              className={classes.content}
            >
              <Grid item xs={12}>
                <Carousel>
                  {caseStudies.nodes.map(caseStudy => (
                    <CarouselCard key={caseStudy.id} data={caseStudy} />
                  ))}
                </Carousel>
              </Grid>
            </Grid>
          </Container>
        )
      }}
    />
  )
}

CaseStudies.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(CaseStudies)
