import React from 'react'
import Layout from '../components/layout/layout'
import SEO from '../components/seo/seo'
import Section from '../components/section/section'
import Services from '../components/sections/services'
import SuccessStories from '../components/sections/case-studies'
import AboutUs from '../components/sections/about-us'
import WhyUs from '../components/sections/why-us'
import ContactUs from '../components/sections/contact-us'
import Partners from '../components/sections/partners'
import Hero from '../components/hero-image/hero-image'
import TechnologiesCard from '../components/technologies-card/technologies-card'

import { sections } from '../lib/menu-nav'

const IndexPage = () => {
  const sectionId = id => {
    const target = sections.filter(section => section.id === id)
    return target[0].path.replace('/#', '')
  }

  return (
    <Layout>
      <SEO title="compose.us GmbH" />

      <Section id="hero">
        <Hero />
      </Section>

      <Section id={sectionId('services')}>
        <Services />
      </Section>

      <Section id={sectionId('case-studies')}>
        <SuccessStories />
      </Section>

      <Section id={sectionId('about-us')}>
        <AboutUs />
      </Section>

      <Section id="technology-card">
        <TechnologiesCard />
      </Section>

      <Section id={sectionId('qualities')}>
        <WhyUs />
      </Section>

      <Section id={sectionId('contact')}>
        <ContactUs />
      </Section>

      <Section id={sectionId('partners')}>
        <Partners />
      </Section>
    </Layout>
  )
}

export default IndexPage
