import React from 'react'
import PropTypes from 'prop-types'

import Box from '@material-ui/core/Box'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import Typography from '@material-ui/core/Typography'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { ChevronRight } from '@material-ui/icons'
import withStyles from '@material-ui/styles/withStyles'
import Image from '../image/image'

const styles = theme => ({
  root: theme.base.section.root,
  actions: {
    color: theme.palette.common.white,
  },
  action: {
    backgroundColor: theme.palette.primary.main,
  },
  photo: {
    maxWidth: theme.spacing(22.5),
    maxHeight: theme.spacing(22.5),
    borderRadius: '50%',
  },
})

const TabPanel = ({ children, value, index, ...other }) => {
  return (
    <Box
      component={'div'}
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {children}
    </Box>
  )
}

TabPanel.propType = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
}

const listItems = (list, icon) => {
  return (
    <List dense={true}>
      {list.map((value, index) => {
        return (
          <ListItem key={index}>
            <ListItemIcon>{icon}</ListItemIcon>
            {value}
          </ListItem>
        )
      })}
    </List>
  )
}

const a11yProps = index => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}

const ProfileCard = ({ classes, photo, name, roles, skills }) => {
  const [value, setValue] = React.useState(0)

  const toggleSection = (event, newValue) => {
    setValue(newValue)
  }

  return (
    <div itemScope={true} itemType={'https://schema.org/Person'}>
      <Card className={classes.root} raised={true}>
        <CardContent>
          <Grid container direction="column">
            <Grid item align={'center'}>
              <Image
                classes={classes.photo}
                src={photo}
                alt={name}
                itemProp={'image'}
              />
              <Typography
                gutterBottom
                variant={'h5'}
                component={'h2'}
                itemProp={'name'}
              >
                {name}
              </Typography>
              <Tabs
                value={value}
                onChange={toggleSection}
                variant="fullWidth"
                className={classes.actions}
                indicatorColor={'primary'}
                aria-label={'2 tabs'}
              >
                <Tab
                  label="Rolle"
                  {...a11yProps(0)}
                  className={classes.action}
                />
                <Tab
                  label="Fhigkeiten"
                  {...a11yProps(1)}
                  className={classes.action}
                />
              </Tabs>
            </Grid>
            <Grid item>
              <TabPanel value={value} index={0}>
                <div itemProp={'jobTitle'}>
                  {listItems(roles, <ChevronRight />)}
                </div>
              </TabPanel>
              <TabPanel value={value} index={1}>
                <div itemProp={'knows'}>
                  {listItems(skills, <ChevronRight />)}
                </div>
              </TabPanel>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </div>
  )
}

ProfileCard.propTypes = {
  classes: PropTypes.object.isRequired,
  photo: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  roles: PropTypes.array.isRequired,
  skills: PropTypes.array.isRequired,
}

export default withStyles(styles)(ProfileCard)
